@font-face {
    font-family: Montserrat;
    src: url("Montserrat/Montserrat-VariableFont_wght.ttf");
}
body {
	margin: 0;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

.main-container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	position: relative;
}

#container {
	z-index: 1;
	width: 100%;
	height: 100vh;
}

p {
	font-family: Montserrat Arial, Helvetica;
}
.overlay {
	opacity: 0.6;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	row-gap: 20px;
	z-index: 99;
	position: absolute;
	bottom: 0;
	left: 0;
	padding: 30px;
	/* background: rgba(255, 255, 255, 0.8); */
}

.buttons li {
	background-color: white;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	text-align: center;
	padding: 15px;
	border: 2px solid wheat;
	box-shadow: 0 0 0 2px white;
	border-radius: 10%;
	margin-bottom: 10px;
	cursor: pointer;
}
.buttons div{
	height: 20px;
	width: 20px;
}

#about-me-img {
	margin-right: 5px;
	background-image: url(public/user.png);
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 100%;
