  font-family: Montserrat Arial, Helvetica;
}

.overlay {
  opacity: .6;
  z-index: 99;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 20px;
  padding: 30px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.buttons li {
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid wheat;
  border-radius: 10%;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 10px;
  padding: 15px;
  display: flex;
  box-shadow: 0 0 0 2px #fff;
}

.buttons div {
  width: 20px;
  height: 20px;
}

